
















import Vue from 'vue'

export default Vue.extend({
  name: 'IconButton',
  props: {
    icon: String,
    scale: String,
    variant: String,
  },
  computed: {
    isMobile(): boolean {
      return window.innerWidth < 992
    },
    paddingStyle(): string {
      return `padding: ${this.isMobile ? '10px 14px 14px' : '12px 14px'}`
    },
  },
})
