












import Vue from 'vue'

export default Vue.extend({
  props: {
    icon: String,
    scale: String,
    variant: String,
  },
  name: 'HoverIcon',
  data() {
    return {
      isHovering: false as boolean,
    }
  },
  methods: {
    getSuffix(): string {
      return (!this.isHovering || this.icon.slice(-5) === '-fill') ? '' : '-fill'
    },
  },
})
